import React, { useState, useEffect } from "react";
import { Container, Image, Row, Col, Form, Button } from "react-bootstrap";
import styles from "./styles.module.css";
import { IconLogoWhite, IconNotif } from "../../assets";
import { Helmet, SlideMenu } from "../../components";
import axios from "axios";
import { Messaege } from "../../utils/helper";
import { URLKLUWYPAY, URLAPI } from "../../utils/configs";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { useLocation, useNavigate } from "react-router-dom";
import { getToken } from "../../storage";

const ConfirmWithdraw = () => {
  const TokenAuth = getToken()
  const location = useLocation();
  const [address] = useState(location?.state?.data?.data?.address);
  const [amount] = useState(location?.state?.data?.data?.amount);
  const [bankCode] = useState(location?.state?.data?.data?.bankCode);
  const navigate = useNavigate();
  console.log(location);

  const postConfirmWithdraw = async () => {
    axios({
      url: URLKLUWYPAY + `/wallet/withdraw`,
      method: "post",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
      data: {
        accId: localStorage.getItem("UserId"),
        address: location?.state?.data?.data?.address,
        amount: amount + AdminFee + setTotalAmount,
        alias: location?.state?.data?.data?.alias,
        bankCode: location?.state?.data?.data?.bankCode,
        remarks: location?.state?.data?.data?.remarks,
        refId: location?.state?.refId,
      },
    })
      .then((response) => {
        Messaege(
          "Succes",
          "Your withdrawal request is now being processed",
          "success"
        );
        setTimeout(() => {
          navigate("/withdraw");
        }, 2000);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const [AdminFee, setAdminFee] = useState("");
  const [adminpercentage, setAdminPercentage] = useState("");

  const getSetting = async () => {
    axios({
      url: URLAPI + "/setting",
      method: "get",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
    })
      .then((response) => {
        setAdminFee(response.data.data.admin_fee);
        setAdminPercentage(response.data.data.admin_percentage);
        console.log(response.data.data, "setting");
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    getSetting();
  }, []);

  let setTotalAmount = ((amount + AdminFee) * adminpercentage) / 100;
  const sparator = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
  return (
    <div className={styles.pages}>
      <Helmet title="Withdraw" />
      <Container className={styles.containerHeader}>
        <Row className={styles.rowHeader}>
          <Col>
            <SlideMenu />
          </Col>
          <Col>
            <Image src={IconLogoWhite} className={styles.imgLogo} />
          </Col>
          <Col style={{ textAlign: "right" }}>
            <Image src={IconNotif} className={styles.imgMenu} />
          </Col>
        </Row>
        {/*  */}

        <Row>
          <Col>
            <p className={styles.TextTitle}>Withdraw</p>
          </Col>
        </Row>
      </Container>
      <Container className={styles.containerQr}>
        <div style={{ marginTop: "20px" }} />
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Withdraw to: {address}
              </Form.Label>
            </Col>
          </Col>
        </Row>
        {/*  */}
        <div style={{ marginTop: "20px" }} />
        {/*  */}
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Bank: {location?.state?.data?.data?.address} {bankCode}
              </Form.Label>
            </Col>
          </Col>
        </Row>
        <div style={{ marginTop: "20px" }} />
        {/*  */}
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Amount: IDR {sparator(amount) }
              </Form.Label>
            </Col>
          </Col>
        </Row>
        <div style={{ marginTop: "20px" }} />
        {/*  */}
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Admin Fee: IDR {sparator(AdminFee)}
              </Form.Label>
            </Col>
          </Col>
        </Row>
        <div style={{ marginTop: "20px" }} />
        {/*  */}
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Admin Percentage Fee: IDR {sparator(setTotalAmount)}
              </Form.Label>
            </Col>
          </Col>
        </Row>
        <div style={{ marginTop: "20px" }} />
        {/*  */}
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Fee: IDR {sparator(location?.state?.data?.data?.totFee)}
              </Form.Label>
            </Col>
          </Col>
        </Row>
        <div style={{ marginTop: "20px" }} />
        {/*  */}
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Total Withdraw (deducted from your account): IDR{" "}
                {sparator(amount +
                  AdminFee +
                  setTotalAmount +
                  location?.state?.data?.data?.totFee)}
              </Form.Label>
            </Col>
          </Col>
        </Row>
        <div style={{ marginTop: "20px" }} />
        {/*  */}
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                You Will Get (will be transferred to you account): IDR{" "}
                {sparator(location?.state?.data?.data?.debitAmount)}
              </Form.Label>
            </Col>
          </Col>
        </Row>
        <Row>
          <Col>
            <Button
              className={styles.btnCancel}
              style={{ backgroundColor: "red", borderColor: "red" }}
            >
              Cancel
            </Button>
          </Col>
          <Col>
            <Button className={styles.btnProceed} onClick={postConfirmWithdraw}>
              Confirm
            </Button>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ConfirmWithdraw;
