import React from "react";
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import { DepositePage, ExchangePage, HomePage, LoginPage, ReferralPage, RegisterPage, SplashScreenPage, ExchangePageDetail, BankPage, WithdrawPage, ChoosePayment, ListDeposit, DepositAmount, QrDeposit, ConfirmWithdraw, Wallet, Transfer } from "../pages";
import Private from "../helpers/PrivateRoute";
const Router = () => {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<SplashScreenPage />} exact />
        <Route path="/login" element={<LoginPage />} />
        <Route path="/register" element={<RegisterPage />} />
        <Route path="/home" element={<Private Component={HomePage} />} />
        <Route path="/bank" element={<Private Component={BankPage} />} />
        <Route path="/wallet" element={<Private Component={Wallet} />} />
        <Route path="/deposit" element={<Private Component={DepositePage} />} />
        <Route path="/exchange" element={<Private Component={ExchangePage} />} />
        <Route path="/exchange/:id" element={<Private Component={ExchangePageDetail} />} />
        <Route path="/referral" element={<Private Component={ReferralPage} />} />
        <Route path="/withdraw" element={<Private Component={WithdrawPage} />} />
        <Route path="/transfer" element={<Private Component={Transfer} />} />
        <Route path="/confirm-withdraw" element={<Private Component={ConfirmWithdraw} />} />
        <Route path="/choosePayment" element={<Private Component={ChoosePayment} />} />
        <Route path="/listDeposit" element={<Private Component={ListDeposit} />} />
        <Route path="/depositAmount" element={<Private Component={DepositAmount} />} />
        <Route path="/qrDeposit" element={<Private Component={QrDeposit} />} />
      </Routes>
    </BrowserRouter>
  );
};

export default Router;