import React, { useState, useEffect } from "react";
import {
  Container,
  Image,
  Row,
  Col,
  InputGroup,
  FormControl,
  Button,
} from "react-bootstrap";
import styles from "./styles.module.css";
import { IconLogoWhite, IconNotif, iconSearch } from "../../assets";
import {
  ButtonComponent,
  FormInput,
  Helmet,
  SlideMenu,
} from "../../components";
import axios from "axios";
import { URLAPI, URLKLUWYPAY } from "../../utils/configs";
import { useLocation, useNavigate } from "react-router-dom";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { v4 as uuidv4 } from "uuid";
import { checkAauth, Messaege } from "../../utils/helper";
import { getToken } from "../../storage";

const DepositAmount = () => {
  const TokenAuth = getToken()
  const location = useLocation();
  const navigate = useNavigate();
  const [type, setType] = useState(location?.state?.type);
  const [amount, setAmount] = useState(0);
  const [cdid, setChid] = useState("");
  const [mid, setMid] = useState("");
  const [user, setUser] = useState([]);
  const [refId, setRefId] = useState("");

  const getDepsiteRoute = async () => {
    axios({
      url: URLKLUWYPAY + "/wallet/deposit/route",
      method: "get",
      headers: {
        type: type === "qris" ? "qris" : "va",
        "access-control-allow-origin": "*",
        "Content-type": "application/json; charset=UTF-8",
      },
    })
      .then((response) => {
        setChid(response?.data?.data[0]?.chId);
        setMid(response?.data?.data[0]?.mId);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    setRefId("UNICOINEX" + uuidv4());
  }, []);

  useEffect(() => {
    async function fetchData() {
      try {
        const savedUser = await AsyncStorage.getItem("user");
        const currentUser = JSON.parse(savedUser);
        setUser(currentUser);
      } catch (error) {
        console.log(error);
      }
    }
    fetchData();
  }, []);

  const postDataAdmin = async () => {
    axios({
      url: URLAPI + "/history",
      method: "post",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
      data: {
        user_id: user?.id,
        amount: parseInt(amount),
        transaksi: type === "qris" ? "Deposit QRIS" : "Deposit Va",
        type: type === "qris" || "va" ? "deposit" : "withdraw",
        refId: refId,
      },
    })
      .then((response) => {})
      .catch((error) => {
        console.log(error);
      });
  };
  const postDeposit = async () => {
    var p = new RegExp(/^[0-9\b]+$/);
    if (p.test(amount)) {
      axios({
        url: URLKLUWYPAY + "/wallet/deposit",
        method: "post",
        data: {
          accId: user?.uuid,
          address: "",
          amount: parseInt(amount),
          bankCode: type === "va" ? location?.state?.bankCode : mid,
          alias: "",
          remarks: "",
          type: type === "qris" ? "qris" : "va",
          addressName: "",
          channelId: cdid,
          refId: refId,
        },
        headers: {
          "access-control-allow-origin": "*",
          "Content-type": "application/json; charset=UTF-8",
        },
      })
        .then((response) => {
          postDataAdmin();
          navigate("/qrDeposit", {
            state: {
              address:
                type === "wallet"
                  ? response.data.data.address
                  : response.data.data,
            },
          });
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      Messaege("Failed", "Please input number", "error");
    }
  };

  const handleChange = (e) => {
    let rawText = e.target.value
    let numberText = rawText.replace(/\D/g, '')
    let result = numberText
    setAmount(Number(result))
}

const _renderNumeric = (value, maxFragtionDigits = 0) => {
  let number = Number(value)
  return number?.toLocaleString('id-ID', {
      maximumFractionDigits: maxFragtionDigits,
      minimumFractionDigits: 0
  })
}

  useEffect(() => {
    getDepsiteRoute();
  }, []);

  return (
    <div className={styles.pages}>
      <Helmet title="Deposit" />
      <Container className={styles.containerHeader}>
        <Row className={styles.rowHeader}>
          <Col>
            <SlideMenu />
          </Col>
          <Col>
            <Image src={IconLogoWhite} className={styles.imgLogo} />
          </Col>
          <Col style={{ textAlign: "right" }}>
            <Image src={IconNotif} className={styles.imgMenu} />
          </Col>
        </Row>
        {/*  */}
        <Row>
          <Col>
            <p className={styles.TextTitle}>Deposit</p>
            <p></p>
          </Col>
        </Row>
      </Container>
      <Container className="mt-4">
        <Row>
          <Col>
            <FormInput
              label="Amount"
              placeholder="Amount"
              name="amount"
              value={_renderNumeric(amount)}
              onChange={handleChange}
              />
            <Button className={styles.buttonBlue} onClick={postDeposit}>
              Proccess
            </Button>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default DepositAmount;
