import React,{useState} from 'react'
import { Container, Image, Row,Col} from 'react-bootstrap'
import styles from './styles.module.css'
import { IconLogoWhite, IconNotif, } from '../../assets'
import { Helmet,  SlideMenu } from '../../components'
import axios from 'axios';
import {  URLKLUWYPAY } from '../../utils/configs'
import { useLocation, useNavigate } from 'react-router-dom'
import QRCode from "react-qr-code";
import { Messaege } from '../../utils/helper'


const QRISDeposit = () => {
  const location = useLocation();
  const [address, setAddress] = useState(location?.state?.address)
  const handleCopy = () => {
    navigator.clipboard.writeText(address?.address)
    Messaege('Succes','Successfully Copy','success');
  }
  return (
    <div className={styles.pages}>
      <Helmet title={address.type === 'va' ? 'VA Deposit': 'QRIS Deposit'}/>
       <Container className={styles.containerHeader}>
        <Row className={styles.rowHeader}>
          <Col>
          <SlideMenu/>
          </Col>
          <Col>
            <Image src={IconLogoWhite} className={styles.imgLogo}/>
          </Col>
          <Col style={{textAlign:'right'}}>
          <Image src={IconNotif} className={styles.imgMenu}/>
          </Col>
        </Row>
        {/*  */}
        <Row>
          <Col>
          <p className={styles.TextTitle}>{address.type === 'va' ? 'VA Deposit': 'QRIS Deposit'}</p>
          <p></p>
          </Col>
        </Row>
    </Container>
    <Container className='mt-4'>
      {address.type === 'va' ? 
      (
       <>
        <Row className='text-center'>
          <Col>
          <div className={styles.card}>
            <p>Rp. {address?.totAmount.toLocaleString()}</p>
            <p className={styles.address}>{address?.address} <span style={{cursor:'pointer'}} onClick={handleCopy}><i class='bx bx-sm bx-fw bx-copy'></i></span></p>
            <h6>{address?.addressName} - {address?.bankCode}</h6>  
          </div>          
          </Col>
        </Row>
       </>
      ) 
      : 
      (
        <Row className="text-center">
        <Col>
        <QRCode
              size={256}
              style={{ height: "auto", maxWidth: "50%", width: "50%" }}
              value={location?.state?.address?.address}
              viewBox={`0 0 256 256`}
              />
        <p className='mt-2'>Scan This QRIS For Deposit</p>
        </Col>
        </Row>
      ) }
     
    </Container>
    </div>
  )
}

export default QRISDeposit