import React, { useEffect, useState } from "react";
import {
  Container,
  Image,
  Row,
  Col,
  Form,
  Button,
  FormControl,
} from "react-bootstrap";
import styles from "./styles.module.css";
import { IconLogoWhite, IconNotif, IconFlag } from "../../assets";
import { Helmet, Required, SlideMenu } from "../../components";
import axios from "axios";
// import { Messaege } from "../../utils/helper";
import { URLAPI, URLKLUWYPAY } from "../../utils/configs";
import { generateString, getInitialName } from "../../helpers/generateString";
import Select from "react-dropdown-select";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { useNavigate } from "react-router-dom";
import { getToken } from "../../storage";
import { v4 as uuidv4 } from "uuid";

const Withdraw = () => {
  const [dataBank, setDataBank] = useState([]);
  const [selectBank, setSelectBank] = useState("");
  const [totalAmount, setTotalAmount] = useState(0);
  const [reffid] = useState(generateString(10));
  const navigate = useNavigate();
  const TokenAuth = getToken()

  const getBankUser = async () => {
    axios({
      url: URLAPI + `/bank-user/` + (await AsyncStorage.getItem("userID")),
      method: "get",
      headers: {
        Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
      },
    })
      .then((response) => {
        console.log(response, "data");
        setDataBank(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const items = dataBank.map((item) => {
    const data = {};
    data.label = item.name + "-" + item.account_number;
    data.value = item.account_number;
    data.bankCode = item.code;
    return data;
  });

  console.log(items);

  useEffect(() => {
    getBankUser();
  }, []);

  const postDataAdmin = async () => {
    axios({
      url: URLAPI + "/history",
      method: "post",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
      data: {
        user_id: await AsyncStorage.getItem("userID"),
        amount: Number(totalAmount),
        transaksi: "Withdraw",
        type: "Withdraw",
        refId: "UNICOINEX" + uuidv4(),
        bank_user_id: ""      
      },
    })
      .then((response) => {})
      .catch((error) => {
        console.log(error);
      });
  };


  const postWithdraw = async () => {
    axios({
      url: URLKLUWYPAY + `/wallet/withdraw/inquiry`,
      method: "post",
      headers: {
        Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
      },
      data: {
        address: selectBank.value,
        amount: Number(totalAmount),
        alias: getInitialName(await AsyncStorage.getItem("username")),
        bankCode: selectBank.bankCode,
        remarks: "WD" + selectBank.value,
        refId: reffid,
      },
    })
      .then((response) => {
        console.log(response);
        if (response?.data?.status === "00") {
          postDataAdmin()
          navigate("/confirm-withdraw", {
            state: {
              data: response.data,
              refId: reffid,
            },
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const handleChange = (e) => {
    let rawText = e.target.value
    let numberText = rawText.replace(/\D/g, '')
    let result = numberText
    setTotalAmount(Number(result))
}

const _renderNumeric = (value, maxFragtionDigits = 0) => {
  let number = Number(value)
  return number?.toLocaleString('id-ID', {
      maximumFractionDigits: maxFragtionDigits,
      minimumFractionDigits: 0
  })
}

const [AdminFee, setAdminFee] = useState("");
  const [AdminPercentage, setAdminpercentage] = useState("");
  const getSetting = async () => {
    axios({
      url: URLAPI + "/setting",
      method: "get",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
    })
      .then((response) => {
        setAdminFee(response.data.data.admin_fee);
        setAdminpercentage(response.data.data.admin_percentage);
        console.log(response.data.data, "setting");
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    getSetting();
  }, []);

  return (
    <div className={styles.pages}>
      <Helmet title="Withdraw" />
      <Container className={styles.containerHeader}>
        <Row className={styles.rowHeader}>
          <Col>
            <SlideMenu />
          </Col>
          <Col>
            <Image src={IconLogoWhite} className={styles.imgLogo} />
          </Col>
          <Col style={{ textAlign: "right" }}>
            <Image src={IconNotif} className={styles.imgMenu} />
          </Col>
        </Row>
        {/*  */}

        <Row>
          <Col>
            <p className={styles.TextTitle}>Withdraw</p>
          </Col>
        </Row>
      </Container>
      <Container className={styles.containerQr}>
        <div style={{ marginTop: "20px" }} />
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Amount
                <Required />
              </Form.Label>
              <FormControl
                className={styles.formSearch}
                placeholder="Amount"
                value={_renderNumeric(totalAmount)}
                onChange={handleChange}
              />
            </Col>
          </Col>
        </Row>
        <Row className="mt-4">
          <p className={styles.textAddres}>Admin Fee : {AdminFee} BIDR </p>
          <p className={styles.textAddres}>
            Admin Percentage : {AdminPercentage}
          </p>
        </Row>
        {/*  */}
        <div style={{ marginTop: "50px" }} />
        {/*  */}
        <Row>
          <Col>
            <p className={styles.textCode}>Choose Bank</p>
            <p className={styles.textAddres}>
              Choose from the following banks you would like to withdraw.
            </p>
            <Select
              placeholder="Select Bank"
              className={styles.formSearch}
              options={items}
              hideSelectedOptions={false}
              value={selectBank}
              onChange={(selected) => setSelectBank(selected[0])}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Button className={styles.btnProceed} onClick={postWithdraw}>
              Proceed
            </Button>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Withdraw;
