import React, { useEffect, useState } from "react";
import {
  Container,
  Image,
  Row,
  Col,
  InputGroup,
  Form,
  DropdownButton,
  Dropdown,
  Button,
} from "react-bootstrap";
import styles from "./styles.module.css";
import { IconLogoWhite, IconNotif, IconFlag, IconExchange } from "../../assets";
import { Helmet, SlideMenu } from "../../components";
import axios from "axios";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { Messaege } from "../../utils/helper";
import { useNavigate } from "react-router-dom";
import { URLAPI, URLKLUWYPAY } from "../../utils/configs";
import { getToken } from "../../storage";

const ExchangeDetail = () => {
  const navigate = useNavigate();
  const [symbol, setSymbol] = useState([]);
  const [setMarket, setSetMaret] = useState("");
  const [paid, setPaid] = useState("");
  const [get, setGet] = useState("");
  const [price, setPrice] = useState("");
  const [takerCommission, setTakerCommission] = useState("");
  const [user, setUser] = useState([]);
  const [tradingfee, setTradingFee] = useState("");
  const [tradingPercentage, setTradingPercentage] = useState("");
  const [paidIdr, setPaidIdr] = useState("");
  const TokenAuth = getToken()

  const titleDropdown = () => {
    return (
      <>
        <Image src={IconFlag} width={24} height={24} />
        <span className={styles.textIDR}>IDR</span>
      </>
    );
  };
  const titleDropdownMarket = () => {
    return (
      <>
        <span className={styles.textIDR}>
          {!setMarket ? "Choose Market" : setMarket}
        </span>
      </>
    );
  };

  const getMarket = async () => {
    axios({
      url: URLKLUWYPAY + "/trade/ticker/price",
      method: "get",
    })
      .then((response) => {
        setSymbol(response.data.data);
        // console.log(response.data.data)
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const getSetting = async () => {
    axios({
      url: URLAPI + "/setting",
      method: "get",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
    })
      .then((response) => {
        setTradingFee(response.data.data.trading_fee);
        setTradingPercentage(response.data.data.trading_percentage);
        console.log(response.data.data, "setting");
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    getMarket();
    getSetting();
  }, []);

  useEffect(() => {
    async function fetchData() {
      try {
        const savedUser = await AsyncStorage.getItem("user");
        const currentUser = JSON.parse(savedUser);
        setUser(currentUser);
      } catch (error) {
        console.log(error);
      }
    }
    fetchData();
  }, []);

  const handleSetMarket = (val) => {
    setSetMaret(val?.symbol);
    setPrice(val?.price);
    setTakerCommission(val?.tradeFee?.takerCommission);
    setGet("");
    setPaid("");
  };

  // const handlePaid = (e) => {
  //   setPaid(e.target.value);
  //   getAmout(e.target.value * price);
  // };

  const getAmout = (val) => {
    setGet((val + (takerCommission * val) + (tradingfee * (val / price))) * ((100 + tradingPercentage) / 100));
  };

  function toMax3Decimals(x) {
    return +x.toFixed(3);
  }

  const proceessExchange = () => {
    axios({
      url: URLKLUWYPAY + "/trade/order",
      method: "post",
      data: {
        accId: user?.uuid,
        symbol: setMarket,
        type: "MARKET",
        side: "BUY",
        quantity: paid,
      },
    })
      .then((response) => {
        console.log(response?.data?.status);
        if (response?.data?.status === "99") {
          Messaege("Failed", response.data.message, "error");
        } else {
          Messaege("Succes", "Successfully Exchange", "success");
          setTimeout(() => {
            navigate("/home");
          }, 1500);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  console.log(get);
  console.log(setMarket);
  // const symbols = symbol.filter(x => x.symbol === "BUSDBIDR");
  const sparator = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  const handleChange = (e) => {
    let rawText = e.target.value;
    let numberText = rawText.replace(/\D/g, "");
    let result = numberText;
    setPaid(Number(result));
    getAmout(e.target.value * price);
  };

  const _renderNumeric = (value, maxFragtionDigits = 0) => {
    let number = Number(value);
    return number?.toLocaleString("id-ID", {
      maximumFractionDigits: maxFragtionDigits,
      minimumFractionDigits: 0,
    });
  };
  return (
    <div className={styles.pages}>
      <Helmet title="Exchange" />
      <Container className={styles.containerHeader}>
        <Row className={styles.rowHeader}>
          <Col>
            <SlideMenu />
          </Col>
          <Col>
            <Image src={IconLogoWhite} className={styles.imgLogo} />
          </Col>
          <Col style={{ textAlign: "right" }}>
            <Image src={IconNotif} className={styles.imgMenu} />
          </Col>
        </Row>
        {/*  */}

        <Row>
          <Col>
            <p className={styles.TextTitle}>Exchange</p>
            <p className={styles.TextSubTitle}></p>
          </Col>
        </Row>
      </Container>
      <Container className={styles.containerQr}>
        <div style={{ marginTop: "20px" }} />

        <Row>
          <Col>
            <InputGroup className={styles.inputGroup}>
              <Form.Control
                className={styles.formControl}
                placeholder="You Get"
                name="get"
                value={_renderNumeric(paid)}
                onChange={handleChange}
              />
              <DropdownButton
                variant="outline-transparent"
                title={titleDropdownMarket()}
                className={styles.dropdownButton}
              >
                {symbol.map((i, idx) => {
                  return (
                    <Dropdown.Item onClick={() => handleSetMarket(i)}>
                      {i?.symbol}
                    </Dropdown.Item>
                  );
                })}
              </DropdownButton>
            </InputGroup>
          </Col>
        </Row>

        <Row className="mt-3">
          <Col xs={10}>
            <p className={styles.rate}>
              Rate of exchange is subject to changes and adjustment in respect
              of any exchange rate fluctuations.
            </p>
          </Col>
          <Col lg={1} md={1} sm={1} xs={2}>
            <Image src={IconExchange} />
          </Col>
        </Row>

        <Row>
          <Col>
            <InputGroup className={styles.inputGroup}>
              <Form.Control
                className={styles.formControl}
                placeholder="You Paid"
                name="paid"
                // disabled={!setMarket ? true : false}
                // onChange={(e) => handlePaid(e)}
                value={!get ? "-" : _renderNumeric(Math.ceil(get))}
              />
              <DropdownButton
                variant="outline-transparent"
                title={titleDropdown()}
                className={styles.dropdownButton}
              ></DropdownButton>
            </InputGroup>
          </Col>
        </Row>

        <Row>
          <Col xs={10} style={{ display: "block" }} className="mt-3">
            <p className={styles.rate1}>
              Price / {setMarket.substring(0, setMarket.length - 4)} :{" "}
              {sparator(price)} BIDR
            </p>
            <p className={styles.rate1}>
              Taker Fee : {takerCommission}{" "}
              {setMarket.substring(0, setMarket.length - 4)}
            </p>
            <p className={styles.rate1}>Trading Fee : {tradingfee} BIDR</p>
            <p className={styles.rate1}>Trading Percentage : {tradingPercentage} </p>

          </Col>
        </Row>

        {/*  */}
        <div style={{ marginTop: "50px" }} />
        <Row>
          <Col>
            <Button className={styles.btnProceed} onClick={proceessExchange}>
              Proceed
            </Button>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ExchangeDetail;
