import React, { useState, useEffect } from "react";
import {
  Container,
  Image,
  Row,
  Col,
  InputGroup,
  Form,
  DropdownButton,
  Dropdown,
  Button,
  FormControl,
} from "react-bootstrap";
import styles from "./styles.module.css";
import {
  IconLogoWhite,
  IconNotif,
  IconQrCodeBlack,
  IconFlag,
  IconExchange,
  iconSearch,
  IconBca,
  IconBri,
  IconBni,
  IconMandiri,
} from "../../assets";
import { Helmet, Required, SlideMenu } from "../../components";
import axios from "axios";
import { Messaege } from "../../utils/helper";
import { URLAPI, URLKLUWYPAY } from "../../utils/configs";
import Select from "react-dropdown-select";
import { useNavigate } from "react-router-dom";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { generateString } from "../../helpers/generateString";
import { getToken } from "../../storage";
import { v4 as uuidv4 } from "uuid";

const Transfer = () => {
  const [dataBank, setDataBank] = useState([]);
  const [selectBank, setSelectBank] = useState("");
  const [amount, setAmount] = useState("");
  const [user, setUser] = useState([]);
  const navigate = useNavigate();
  let generateStrings = generateString(10);
  const TokenAuth = getToken()

  // wallet
  const getBank = async () => {
    axios({
      url: URLAPI + `/wallet-user/` + (await AsyncStorage.getItem("userID")),
      method: "get",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
    })
      .then((response) => {
        console.log(response.data.data, "data wallet user");
        setDataBank(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  
  const postDataAdmin = async () => {
    axios({
      url: URLAPI + "/history",
      method: "post",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
      data: {
        user_id: await AsyncStorage.getItem("userID"),
        amount: Number(amount),
        transaksi: "Transfer",
        type: "Transfer",
        refId: "UNICOINEX" + uuidv4(),
        wallet_user_id: selectBank.value
      },
    })
      .then((response) => {})
      .catch((error) => {
        console.log(error);
      });
  };

  const items = dataBank.map((item) => {
    const data = {};
    data.label =
      item.user.username + " - " + item.account_number + " - " + item.coin?.coin;
    data.coin = item.coin === null ? null : item.coin.coin;
    data.network = item.network === null ? null : item.network.network;
    data.name = item.account_number;
    data.accid = item.user.uuid;
    data.value = item.id;
    return data;
  });
  console.log(items, "")

  useEffect(() => {
    getBank();
  }, []);

  useEffect(() => {
    async function fetchData() {
      try {
        const savedUser = await AsyncStorage.getItem("user");
        const currentUser = JSON.parse(savedUser);
        setUser(currentUser);
      } catch (error) {
        console.log(error);
      }
    }
    fetchData();
  }, []);

  console.log(selectBank);

  const postWithdraw = async () => {
    axios({
      url: URLKLUWYPAY + `/trade/transfer`,
      method: "post",
      headers: {
        Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
      },
      data: {
        accId: selectBank.accid,
        coin: selectBank.coin,
        transferId: "Unicoin" + generateStrings,
        network: selectBank.network,
        address: selectBank.name,
        amount: Number(amount),
        transactionFeeFlag: false,
        name: selectBank.name,
        timestamp: new Date().getTime(),
      },
    })
      .then((response) => {
        console.log(response);
        if (response?.data?.status === "99") {
          Messaege("Failed", "Failed transfer", "error");
        } else {
          postDataAdmin()
          Messaege("Succes", "Successfully Exchange", "success");
          setTimeout(() => {
            navigate("/home");
          }, 1500);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const handleChange = (e) => {
    let rawText = e.target.value;
    let numberText = rawText.replace(/\D/g, "");
    let result = numberText;
    setAmount(Number(result));
  };

  const _renderNumeric = (value, maxFragtionDigits = 0) => {
    let number = Number(value);
    return number?.toLocaleString("id-ID", {
      maximumFractionDigits: maxFragtionDigits,
      minimumFractionDigits: 0,
    });
  };

  const [AdminFee, setAdminFee] = useState("");
  const [AdminPercentage, setAdminpercentage] = useState("");
  const getSetting = async () => {
    axios({
      url: URLAPI + "/setting",
      method: "get",
      headers: {
        Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
      },
    })
      .then((response) => {
        setAdminFee(response.data.data.admin_fee);
        setAdminpercentage(response.data.data.admin_percentage);
        console.log(response.data.data, "setting");
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    getSetting();
  }, []);

  return (
    <div className={styles.pages}>
      <Helmet title="Withdraw" />
      <Container className={styles.containerHeader}>
        <Row className={styles.rowHeader}>
          <Col>
            <SlideMenu />
          </Col>
          <Col>
            <Image src={IconLogoWhite} className={styles.imgLogo} />
          </Col>
          <Col style={{ textAlign: "right" }}>
            <Image src={IconNotif} className={styles.imgMenu} />
          </Col>
        </Row>
        {/*  */}

        <Row>
          <Col>
            <p className={styles.TextTitle}>Transfer</p>
          </Col>
        </Row>
      </Container>
      <Container className={styles.containerQr}>
        <div style={{ marginTop: "20px" }} />
        <Row>
          <Col>
            <Col>
              <Form.Label className={styles.labelForm}>
                Amount
                <Required />
              </Form.Label>
              <FormControl
                className={styles.formSearch}
                placeholder="Amount"
                value={_renderNumeric(amount)}
                onChange={handleChange}
              />
            </Col>
          </Col>
        </Row>
        <Row className="mt-4">
          <p className={styles.textAddres}>Admin Fee : {AdminFee} BIDR </p>
          <p className={styles.textAddres}>
            Admin Percentage : {AdminPercentage}
          </p>
        </Row>
        {/*  */}
        <div style={{ marginTop: "40px" }} />
        {/*  */}
        <Row>
          <Col>
            <p className={styles.textCode}>Choose Wallet</p>
            <p className={styles.textAddres}>
              Choose from the following wallet you would like to Transfer.
            </p>
            <Select
              placeholder="Select Bank"
              className={styles.formSearch}
              hideSelectedOptions={false}
              options={items}
              value={dataBank}
              onChange={(selected) => setSelectBank(selected[0])}
            />
          </Col>
        </Row>

        <Row>
          <Col>
            <Button className={styles.btnProceed} onClick={postWithdraw}>
              Proceed
            </Button>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Transfer;
