import React, { useState, useEffect } from "react";
import {
  Container,
  Image,
  Row,
  Col,
  InputGroup,
  Form,
  DropdownButton,
  Dropdown,
  Button,
  FormControl,
} from "react-bootstrap";
import styles from "./styles.module.css";
import {
  IconLogoWhite,
  IconNotif,
  IconQrCodeBlack,
  IconFlag,
  IconExchange,
  iconSearch,
  IconBca,
  IconBri,
  IconBni,
  IconMandiri,
} from "../../assets";
import { Helmet, Required, SlideMenu } from "../../components";
import axios from "axios";
import { Messaege } from "../../utils/helper";
import { URLAPI } from "../../utils/configs";
import Select from "react-dropdown-select";
import { useNavigate } from "react-router-dom";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { getToken } from "../../storage";

const Bank = () => {
  const [dataBank, setDataBank] = useState([]);
  const [selectCoin, setSelectCoin] = useState("");
  const [selectNetwork, setSelectNetwork] = useState("");
  const [accountNumber, setAccountNumber] = useState("");
  const [otpNumber, setOtpNumber] = useState("");
  const [bank, setBank] = useState([]);
  const [bankUser, setBankUser] = useState([]);
  const [user, setUser] = useState([]);
  const [succes, setSuccsess] = useState("");
  const [hasilOtp, sethasilOtp] = useState("");
  const [coin, setCoin] = useState([]);
  const [network, setNetwork] = useState([]);
  const navigate = useNavigate();
  const TokenAuth = getToken()

  // Bank user
  const getBankUser = async () => {
    axios({
      url: URLAPI + `/wallet-user/` + (await AsyncStorage.getItem("userID")),
      method: "get",
      headers: {
        Authorization: "Bearer " + TokenAuth,
      },
    })
      .then((response) => {
        setBankUser(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  // const getBank = async () => {
  //   axios({
  //     url: URLAPI + "/wallet",
  //     method: "get",
  //     headers: {
  //       Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
  //     },
  //   })
  //     .then((response) => {
  //       console.log(response);
  //       setDataBank(response.data.data);
  //     })
  //     .catch((error) => {
  //       console.log(error);
  //     });
  // };

  const getcoin = async () => {
    axios({
      url: URLAPI + "/coin",
      method: "get",
      headers: {
        Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
      },
    })
      .then((response) => {
        console.log(response, "coin");
        setCoin(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const getNetwork = async () => {
    axios({
      url: URLAPI + `/network/${selectCoin}`,
      method: "get",
      headers: {
        Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
      },
    })
      .then((response) => {
        console.log(response, "coin");
        setNetwork(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const items = network.map((item) => {
    const data = {};
    data.label = item.name + " - " + item.network;
    data.value = item.id;
    return data;
  });
  const items3 = coin.map((item) => {
    const data = {};
    data.label = item.coin;
    data.value = item.id;
    return data;
  });
  console.log(items3, "test");

  useEffect(() => {
    // getBank();
    getBankUser();
  }, []);

  useEffect(() => {
    async function fetchData() {
      try {
        const savedUser = await AsyncStorage.getItem("user");
        const currentUser = JSON.parse(savedUser);
        setUser(currentUser);
      } catch (error) {
        console.log(error);
      }
    }
    fetchData();
  }, []);

  const handlePost = async () => {
    if (hasilOtp) {
      axios({
        url: URLAPI + "/wallet-user",
        method: "post",
        headers: {
          Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
        },
        data: {
          user_id: await AsyncStorage.getItem("userID"),
          coin_id: selectCoin, 
          network_id: selectNetwork, 
          account_number: accountNumber,
        },
      })
        .then((response) => {
          setAccountNumber("");
          if (response?.data?.status === "99") {
            Messaege("Failed", "Failed Exchange", "error");
          } else {
            Messaege("Succes", "Successfully Add data", "success");
            setTimeout(() => {
              navigate("/home");
            }, 1500);
          }
          getBankUser();
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      Messaege("Failed", "OTP Wrong", "error");
    }
  };

  const getOtp = async () => {
    axios({
      url: URLAPI + "/request-otp",
      method: "post",
      headers: {
        Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
      },
      data: {
        user_id: await AsyncStorage.getItem("userID"),
      },
    })
      .then((response) => {
        Messaege("Succes", "Successfully get otp", "success");
        console.log(response);
        setSuccsess(response.data.success);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const postOtp = async () => {
    axios({
      url: URLAPI + "/match-otp",
      method: "post",
      headers: {
        Authorization: "Bearer " + (await AsyncStorage.getItem("token")),
      },
      data: {
        user_id: await AsyncStorage.getItem("userID"),
        otp_number: hasilOtp,
      },
    })
      .then((response) => {
        console.log(response);
        setDataBank(response.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    if (succes === true) {
      postOtp();
    }
    getcoin();
    if (selectCoin) {
      getNetwork();
    }
  }, [selectCoin]);

  return (
    <div className={styles.pages}>
      <Helmet title="Wallet" />
      <Container className={styles.containerHeader}>
        <Row className={styles.rowHeader}>
          <Col>
            <SlideMenu />
          </Col>
          <Col>
            <Image src={IconLogoWhite} className={styles.imgLogo} />
          </Col>
          <Col style={{ textAlign: "right" }}>
            <Image src={IconNotif} className={styles.imgMenu} />
          </Col>
        </Row>
        {/*  */}

        <Row>
          <Col>
            <p className={styles.TextTitle}>Wallet</p>
            <p className={styles.TextSubTitle}>
              Add and save your favorite wallet here.
            </p>
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroup className="mb-4">
              <FormControl
                className={styles.formSearch}
                placeholder="Search Wallet"
              />
              <InputGroup.Text className={styles.borderButton}>
                <Image src={iconSearch} width={15} height={15} />
              </InputGroup.Text>
            </InputGroup>
          </Col>
        </Row>
      </Container>
      <Container className={styles.containerQr}>
        <Row>
          <Col>
            <p className={styles.textCode}>My Wallet Adress List</p>
            <hr />
            {/* <Row>
              <Col lg={6} md={6} sm={6} className={styles.textCode}>
                Wallet Adress
              </Col>
            </Row> */}
            <Row
              className="mt-3 mb-3"
              style={{ maxHeight: 200, overflowX: "auto" }}
            >
              <Col>
                {/* <hr /> */}
                {bankUser?.map((i, idx) => {
                  return (
                    <>
                      <Row>
                        <Col>
                          <span className={styles.textCode}>
                            {i?.account_number}
                          </span>
                        </Col>
                      </Row>
                      <hr />
                    </>
                  );
                })}
              </Col>
            </Row>
          </Col>
        </Row>
        <Row className="mb-4">
          <Col>
            <p className={styles.textCode}>Select Coin</p>
            <p className={styles.textAddres}>Choose and enter your Coin.</p>
            <div style={{ height: 10 }} />
            <Select
              placeholder="Select Coin"
              className={styles.formSearch}
              options={items3}
              hideSelectedOptions={false}
              value={coin}
              onChange={(selected) => setSelectCoin(selected[0].value)}
            />
          </Col>
        </Row>
        {/*  */}
        <Row>
          <Col>
            <p className={styles.textCode}>Select Network</p>
            <p className={styles.textAddres}>Choose and enter network.</p>
            <div style={{ height: 10 }} />
            <Select
              placeholder="Select Network"
              className={styles.formSearch}
              options={items}
              hideSelectedOptions={false}
              value={network}
              onChange={(selected) => setSelectNetwork(selected[0].value)}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Form.Label className={styles.labelForm}>
              Wallet Adress
              <Required />
            </Form.Label>
            <FormControl
              className={styles.formSearch}
              placeholder="Wallet Adress"
              name="accountNumber"
              value={accountNumber}
              onChange={(e) => setAccountNumber(e.target.value)}
            />
          </Col>
        </Row>
        <Row className="mt-3">
          <Form.Label className={styles.labelForm}>
            OTP
            <Required />
          </Form.Label>
          <Col lg={6}>
            <FormControl
              className={styles.formSearch}
              placeholder="OTP Number"
              name="hasilOtp"
              value={hasilOtp}
              onChange={(e) => sethasilOtp(e.target.value)}
            />
          </Col>
          <Col lg={6}>
            <p className={styles.textCodeOTP} onClick={getOtp}>
              Send OTP
            </p>
          </Col>
        </Row>
        <Row>
          <Col>
            <Button className={styles.btnProceed} onClick={handlePost}>
              Add
            </Button>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Bank;
